
import React, { Component } from 'react';

/**
 * @param {string} advertiser
 */
export default class extends Component {
	render () {
		let logo;

		if(this.props.type === 'paid-post') {
			logo = (<span className="card__advertiser-logo">Paid Post</span>);
		} else {
			logo = (<span className="card__advertiser-text">Promoted Content</span>);
		}

		return (
			<div className="card__advertiser">
				{logo} BY <span className="card__advertiser-name">{this.props.advertiser}</span>
			</div>
		);
	}
}



