export default (item, opts, { flags = {} }) => {

	const video = {
		type: 'video',
		title: item.title,
		flags: flags,
		modifier: opts.modifier
	};

	video.id = item.videoId || item.id

	if (item.summary && opts.standfirst) {
		video.standfirst = Object.assign({}, opts.standfirst, { text: item.summary });
	}

	return video;
};



