import React, { Component } from 'react';
import { Image } from '@financial-times/n-image';

import { renderClasses } from '../../src/lib/helpers';

const headshotSize = 60;
const getImage = (headshot, lazyLoadHeadshot) =>
	headshot ?
		<Image
			classes="card__brand__image card__brand__image--headshot"
			url={`${headshot}?source=next&fit=scale-down&compression=best`}
			width={headshotSize}
			height={headshotSize}
			widths={[headshotSize, 2 * headshotSize]}
			lazyLoad={lazyLoadHeadshot} /> :
		<div className="card__brand__image">
			<div className="card__brand__quote card__brand__quote--left" />
			<div className="card__brand__quote card__brand__quote--right" />
		</div>;

/**
 * @param {string} title
 * @param {string} [url]
 * @param {string} [type] - opinion|editors-pick
 * @param {string} [size = small]
 * @param {string} [headshot]
 * @param {boolean} [lazyLoadHeadshot = false]
 */
export default class extends Component {
	render () {
		const brand = this.props;
		const size = brand.size || 'tiny';
		const classes = {
			'card__brand': true,
			[`card__brand--${size}`]: true,
			[`card__brand--${brand.type}`]: brand.type,
			'card__brand--with-headshot': brand.headshot
		};

		return (
			<div className={renderClasses(classes)} data-trackable="brand">
				{brand.type === 'opinion' ? getImage(brand.headshot, brand.lazyLoadHeadshot) : null}
				<p className="card__brand__title">
					{
						brand.url ?
							<a className="card__brand__link" href={brand.url} data-trackable="link">
								{brand.title}
							</a> :
							brand.title
					}
				</p>
			</div>
		);

	}
}



