export default (item, opts) => {
	const article = {
		type: item.type,
		id: item.id,
		title: item.title,
		url: item.url,
		advertiser: item.advertiser,
		impressionURL: item.impressionURL,
		published: item.published,
		lastPublished: item.lastPublished,
		lazyLoadImages: opts.lazyLoadImages,
		tag: item.tag
	};


	if (opts.image && item.image && item.image.url) {
		article.image = Object.assign({}, opts.image, item.image);
	}

	if (opts.standfirst) {
		article.standfirst = Object.assign({}, opts.standfirst, { text: item.summary });
	}

	if (!opts.hideTag && item.tag) {
		article.tag = item.tag;
	}

	return article;
};



