import React, { Component } from 'react';
import Follow from '@financial-times/n-ui/myft/templates/follow';

/**
 * @param {string} id
 * @param {string} name
 * @param {string} url
 * @param {string} taxonomy
 * @param {boolean} [isFollowable]
 */
export default class extends Component {
	render () {
		const tag = this.props;
		const isFollowable = !!(tag.isFollowable && tag.flags && tag.flags.myFtApiWrite);

		return (
			<div className="card__tag">
				{ this.props.isSpecialReport ? 'Special Report: ' : ''}
				<a className="card__tag__link" href={tag.url} data-trackable="primary-tag">
					{tag.name}
				</a>
				{
					isFollowable ?
						<Follow conceptId={tag.id} name={tag.name} taxonomy={tag.taxonomy} classes="card__tag__follow" /> :
						null
				}
			</div>
		);
	}
}



