import React, { Component } from 'react';
import Follow from '@financial-times/n-ui/myft/templates/follow';
import Sponsorship from '../meta-sponsorship/meta-sponsorship';

export default class extends Component {
	render () {

		const followButton = this.props.followButton ?
			(<Follow {...this.props.followButton} />) : null

		const sponsorship = this.props.sponsorshipInfo ?
			(<Sponsorship sectionId={this.props.sectionId} {...this.props.sponsorshipInfo} />) : null

		return (
			<div className="section-meta">
				<h2 className="section-meta__title" dangerouslySetInnerHTML={{ __html: this.props.title }} />
				{ followButton }
				{ sponsorship }
				</div>
		)
	}
};



