import React, { Component } from 'react';
import { Image } from '@financial-times/n-image';
import { renderClasses } from '../../src/lib/helpers';

/**
 * @param {string} url
 * @param {number[]} widths
 * @param {Object} sizes
 * @param {string} contentUrl
 * @param {boolean} [lazyLoad = false]
 * @param {number} [ratio = 16 / 9] width / height of the image
 */
export default class extends Component {
	render () {
		const image = this.props;
		const classNames = {
			'card__image-link': true,
			'card__image-link--portrait': image.ratio && image.ratio < 1,
			'card__image-link--has-content-overlay': image.hasContentOverlay
		};

		return (
			<a
				className={renderClasses(classNames)} href={image.contentUrl}
				data-trackable="image"
				aria-hidden="true">
				<Image
					url={image.url}
					widths={image.widths || []}
					sizes={image.sizes || {}}
					lazyLoad={image.lazyLoad}
					placeholder={image.ratio || 'landscape'} />
			</a>
		);
	}
}



