import React from 'react';

const statuses = {
	inprogress: 'in-progress',
	comingsoon: 'coming-soon',
	closed: 'closed'
};

/**
 * @param {string} status - inprogress|commingsoon|closed
 */
export default ({ status }) => {
	return (
		<div className={`card__live-blog-badge card__live-blog-badge--${statuses[status]}`}>
			<span className="card__live-blog-badge__text">
				live
			</span>
		</div>
	)
};



