import React, {Component} from 'react';

import { responsiveValue } from '../../src/lib/helpers';

/**
 * @param {Object[]) items
 * @param {string) items[].id
 * @param {string) items[].url
 * @param {string) items[].title
 * @param {boolean) items[].isPremium
 * @param {Object} [show]
 */
export default class extends Component {
	render () {
		const relatedEls = this.props.items.map(item =>
			<li className="card__related-item o-card__related-content-item" key={item.id}>
				<a href={item.url} className="card__related-item__link" data-trackable="related" >
					<span className="card__classifier-gap">
						{item.title}
					</span>
					{this.props.flags && this.props.flags.premiumContentIndicator && item.isPremium ? (
						<span className="card__classifier">Premium</span>
					) : null}
				</a>
			</li>
		);
		const dataShow = responsiveValue(this.props.show || { default: true });

		return (
			<ol className="card__related-items o-card__related-content o-grid-row" data-show={dataShow}>
				{relatedEls}
			</ol>
		);
	}
}



