import React from 'react';

/**
 * @param {string} title
 * @param {string} url
 * @param {boolean} [isPremium = false]
 */
export default ({ title, url, isPremium }) => {
	// note the card__classifier-gap is because we want the :after to not pick up
	// card__title-text's dotted border
	return (
		<a className="card__title" href={url} data-trackable="main-link">
			<span className="card__classifier-gap">
				<h3 className="card__title-text">
					{title ? title.trim() : ''}
				</h3>
			</span>
			{isPremium ? (
					<span className="card__classifier">Premium</span>
			) : null}
		</a>
	);
};



